<template>
  <div id="app">
    <v-app>
      <v-main>
        <div style="position:absolute;display:flex; width:100%; flex-direction: column; height: 100%;">
          <v-text-field tile solo label="Search Account" prepend-inner-icon="mdi-magnify" elevation="0" clearable flat
            hide-details v-model="search_text" height="40" style="max-height: 40px"></v-text-field>
          <v-divider></v-divider>
          <div class="pa-1" style="flex:1; overflow:auto">
            <v-list class="py-0" dense style="height:100%; overflow:auto; background: transparent;">

              <v-list-item-group color="white">
                <template v-for="(character, i) in filtered_character">
                  <v-list-item two-line :key="i" :disabled="!ready">
                    <v-list-item-content @click="EnterGame(character)">
                      <v-list-item-title> {{ character.nickname }}</v-list-item-title>
                      <v-list-item-subtitle>{{ character.workspace_mode }}</v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>
                </template>
              </v-list-item-group>
            </v-list>
          </div>
        </div>
      </v-main>
    </v-app>

  </div>
</template>

<script>
import "@/style.scss"
import axios from "axios"
const SERVER_ADDRESS = "http://192.168.1.99:3000"
export const request = async(path, config = null) => new Promise( (resolve,reject) => {
    axios.get(`${SERVER_ADDRESS}/${path}`, config).then(({ data}) => {
        console.log("axios data",data.data)
        resolve(data.data)
    }).catch(err => {
        reject(err)
    })
} )

export const post = (path, data = null, config = null) => axios.post(`${SERVER_ADDRESS}/${path}`, data, config)

export default {
  data: () => ({
    characters: [],
    ready: true,
    search_text: ""

  }),
  computed: {
    filtered_character() {
      console.log(this.characters, this.characters.length)
      return this.characters.filter(a => {
        if (a.nickname) {
          return a.nickname.toLowerCase().includes(this.search_text.toLowerCase())
        }
        return true
      })
    }
  },

  async mounted() {
    this.characters = await request("character")
  },

  methods: {
    EnterGame(character) {
      this.ready_state = false
      console.log(character)
      post(`character/${character.nickname}/auth_cookie`).then((res) => {
        if (res.status == 200) {
          console.log(res)
          procedure({ Data: res.data.data })
        }
      }).catch(ex => {
        alert(ex)
        console.log("Failed fetching account:", ex)
      })

      setTimeout(() => {
        this.ready = true
      }, 1000)
    },
  }

}
</script>
